import React from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components'
import Loadable from 'react-loadable'
import { Comment, Header } from 'semantic-ui-react'
import { fetchPostsIfNeeded } from '../../actions/article'
import HeaderNav from '../../components/HeaderNav'
import Loader from '../../components/Loader'
import CommentForm from '../../components/CommentForm'
// import CommentList from '../../components/CommentList'
import './github-markdown.css'
import './details.css'

const LoadableCommentForm = Loadable({
  loader: () => import('../../components/CommentList'),
  loading: () => <div>Loading...</div>,
});

const Content = styled.div`
  max-width: 1080px;
  margin: 0 auto;
  padding: 100px 0;
  text-align: left;
`

class Details extends React.Component {
  componentDidMount () {
    const { dispatch, match } = this.props
    const id = match.url.slice(7)

    dispatch(fetchPostsIfNeeded(id))
  }

  render () {
    // 
    // state
    const {
      author, content, isFetching,replies, title,
      create_at, visit_count, reply_count
    } = this.props.article

    return (
      <div className='details'>
        <HeaderNav title='' icon='true' history={this.props.history} />
        <Content>
          { isFetching
            ? <Loader />
            : <div className='content-box'>
              <Comment.Group>
                <Comment>
                  <Comment.Avatar as='a' src={author.avatar_url} alt={author.loginname} />
                  <Comment.Content>
                    <Comment.Author>{author.loginname}</Comment.Author>
                    <Comment.Actions>
                      <Comment.Action>{create_at}</Comment.Action>
                      <Comment.Action>{visit_count}</Comment.Action>
                      <Comment.Action>{reply_count}</Comment.Action>
                    </Comment.Actions>
                  </Comment.Content>
                </Comment>
              </Comment.Group>
              <Header as='h2' block textAlign='left' size='large'>{ title }</Header>
              <div className='markdown-body'>
                {/* dangerouslySetInnerHTML  React  DOM  innerHTML */}
                <div dangerouslySetInnerHTML={{__html: content}} />
              </div>
              <div className='ui horizontal divider'> </div>
              <div className='ui comments'>
                <LoadableCommentForm
                  replies={replies}
                  article={this.props.article}
                  auth={this.props.auth}
                  dispatch={this.props.dispatch}
                />
              </div>

              {/*  */}
              <CommentForm
                article={this.props.article}
                auth={this.props.auth}
                dispatch={this.props.dispatch}
              />
            </div>
          }
        </Content>
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { article, auth } = state
  return {
    article,
    auth
  }
}

export default connect(mapStateToProps)(Details)



